export default "import { setupIosPicker } from './EmblaCarouselIosPicker';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst LOOP = false;\n\nconst wheelNodes = document.querySelectorAll('.embla__ios-picker__viewport');\nconst iosPickerNodes = Array.from(wheelNodes);\n\n// @ts-ignore\nconst iosPickers = iosPickerNodes.map((iosPickerNode) => setupIosPicker(iosPickerNode, {\n    dragFree: true,\n    containScroll: false,\n    loop: LOOP,\n    axis: 'y',\n    watchSlides: false\n}));\n";